.multi-button {
  flex-direction: row !important;
  margin-top: 1rem;
  align-items: center;
  justify-content: center;
  // height: 40px;
}
.social-icons {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  margin-right: 25px;
}

.loader {
  color: black;
  aspect-ratio: 1/1;
  border-radius: 50%;
  animation-name: spin;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  width: 20px;
  border: 5px dotted #fff;
  animation-duration: 5s;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.paste-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
